:root {
    --scroll_bg: rgba(0, 0, 0, 0.11);
    --scroll_border: #c9c9c9;
    --scroll_bg_hover: #c3c3c3;
    --headersTitle: #006ae4;
}

body[scheme="space_gray"] {
    --scroll_bg: #303030;
    --scroll_border: #6060602b;
    --scroll_bg_hover: #3f3f3f;
    --headersTitle: var(--accent);
}

/* Отступы */
.Div {
    margin: 12px;
}

/* не знаю */
* {
    outline: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
}

.Tabs--buttons .Tabs__in>.TabsItem:first-child {
    margin-left: 12px;
}

.ActionSheetItem__marker {
    margin-left: 12px;
}

/* Шрифт шапки */
.PanelHeader__content, .Title--w-medium, .PanelHeader__content-in, .ModalPageHeader__content-in {
    font-family: 'Jost', sans-serif;
    letter-spacing: 0.6px;
    font-weight: 800;
}

.Title--w-medium {
    font-weight: 500;
}

/* Поля ввода */
textarea, input, .input {
    width: 100%;
    display: block;
    box-sizing: border-box;
    padding: 9px 11px 9px 11px;
    font-size: 12pt;
    border: 0;
    word-break: break-all;
    color: var(--text_primary);
    background: transparent;
}

input, .input, .input *, textarea:not(.unselect) {
    -webkit-touch-callout: text;
    -webkit-user-select: text;
    user-select: text;
}

input, .input, .input *, textarea {
    z-index: 1;
    position: relative;
}

textarea {
    resize: none;
}

.txt-bg {
    z-index: 0;
    background: var(--field_background);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.withTitle {
    padding-top: 21px;
}

.errorInput .border {
    border: 1px solid var(--field_error_border);
}
.errorInput .txt-bg {
    background: var(--field_error_background);
}

/* Границы */
.border {
    position: absolute;
    z-index: 3;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    border: 1px solid var(--field_border);
    pointer-events: none;
    transition: border 0.2s;
}

/* Округления */
.border--vkcom {
    border-radius: 6px;
}

.border--ios {
    border-radius: 10px;
}

.border--android {
    border-radius: 8px;
}

/* Заголовки полей ввода (да) */
.inputTitle {
    position: absolute;
    z-index: 1;
    font-size: 9pt;
    left: 10px;
    top: 5px;
    opacity: 0.7;
    font-weight: 600;
}

.txtTitle {
    position: absolute;
    z-index: 2;
    font-size: 9pt;
    top: 0;
    left: 0;
    right: 0;
    font-weight: 600;
    background: var(--field_background);
    padding-left: 10px;
    padding-top: 5px;
}

.txtTitle div {
    opacity: 0.7;
}

/* Список методов */
.methodList {
    display: flex;
    width: 100%;
    cursor: grab;
}

.methodList button {
    margin-right: 4px;
    flex: 1;
    cursor: grab !important;
}

.methodList::before {
    content: '';
    padding-left: 12px;
}

.methodList::after {
    content: '';
    padding-left: 12px;
}

/* Сепаратор наверное */
.sep {
    height: 1px;
    background: var(--separator_common);
}

/* Заголовок */
.title {
    text-transform: uppercase;
    opacity: 0.5;
    font-weight: 700;
    font-size: 10pt;
    margin-bottom: 2px;
}

/* Заголовки шапок (че несет) */
.headersTitle {
    display: inline;
    color: var(--headersTitle);
}

/* Превью HTML */
iframe {
    display: block;
    width: 100%;
    border-radius: 12px;
    background: #ffffff;
    border: 1px solid var(--field_border);
}

/* не понимаю... */
.placeholder {
    margin: 56px 0;
    opacity: 0.6;
}

.notice {
    position: relative;
    color: var(--dynamic_orange);
    padding: 10px 10px 10px 44px;
    border: 2px solid var(--dynamic_orange);
    font-weight: 500;
}

.notice .icon {
    position: absolute;
    left: 10px;
    top: 10px;
}

.deleteButton {
    background: var(--destructive);
    color: var(--white);
    width: 26px;
    height: 26px;
    margin-left: 12px;
    border-radius: 50%;
    position: relative;
}

.deleteButton:before {
    height: 2px;
    border-radius: 2px;
    background: var(--white);
    content: '';
    position: absolute;
    margin: 12px 0;
    left: 6.5px;
    right: 6.5px;
}

code {
    background: var(--accent);
    color: var(--background_content);
    padding: 1px 4px;
    display: inline-block;
    font-family: monospace;
    font-size: 11pt;
    border-radius: 2px;
}

/* Скролл */
.SplitCol::-webkit-scrollbar {
    width: 14px;
}

.SplitCol::-webkit-scrollbar-thumb {
    border: 4px solid rgba(0, 0, 0, 0);
    background-clip: padding-box;
    border-radius: 7px;
    background-color: var(--scroll_bg);
    box-shadow: inset -1px -1px 0px var(--scroll_border), inset 1px 1px 0px var(--scroll_border);
}

.SplitCol::-webkit-scrollbar-thumb:hover {
    background-color: var(--scroll_bg_hover);
}

.SplitCol::-webkit-scrollbar-corner {
    background-color: transparent;
}

.SplitCol::-webkit-scrollbar-button:single-button:vertical:decrement {
    height: calc(var(--panelheader_height_ios) + 12px);
}

.SplitCol::-webkit-scrollbar-button:single-button:vertical:increment {
    height: 12px;
}